.navigation {
    position: fixed;
    width: 100%;
    z-index: 1;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 20px;
    background-color: #EFEEEC;
}

.navigation__logo {
    width: 100px;
    height: 100px;
}

.navigation a {
    text-decoration: none;
    color: #000;
    font-weight: 600;
}