.section {
    position: absolute;
    top: 140px;
    width: 100%;
    height: 100vh;
    padding: 10px 20px;
    display: flex;
    justify-content: center;
    margin-top: 80px;
}

.loader {
    display: flex;
    justify-content: center;
    align-items: center;
    animation: rotation 2s ease-in-out;
}

.movies {
    display: grid;
    grid-template-columns: repeat(2, minmax(500px, auto));
    grid-gap: 100px;
}

@keyframes rotation {
    from {
        transform: rotate(0);
    }to {
        transform: rotate(359deg);
    }
}

@media screen and (max-width: 1090px) {
    .movies {
        grid-template-columns: 1fr;
    }
}