.search {
    margin-top: 0;
}

.search__form {
    text-align: center;
}

.search__input {
    width: 400px;
    height: 40px;
    border: none;
    border-bottom: 2px solid tomato;
    outline: none;
    font-size: 24px;
    text-align: center;
    padding: 4px;
    transition: all 300ms ease-in-out;
}

.search__input:focus {
    width: 80%;
}

svg {
    background-color: #ccc;
}

.svg-inline--fa.fa-w-16 {
    width: 24px;
    height: 24px;
}

.svg-inline--fa {
    font-size: 24px;
}

.search .movies {
    margin: 80px 0;
}

@media screen and (max-width: 1090px) {
    .search__input {
        width: 200px;
    }

    .search__input:focus {
        width: 100%;
    }
}