.movie {
    background-color: #EFEEEC;
    border-radius: 5px;
    padding: 16px;
    -webkit-box-shadow: 6px 6px 11px -4px rgba(204,204,204,1);
    -moz-box-shadow: 6px 6px 11px -4px rgba(204,204,204,1);
    box-shadow: 6px 6px 11px -4px rgba(204,204,204,1);
}

.movie a {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    text-decoration: none;
    color: #000;
}

.movie__image {
    position: relative;
    top: -50px;
    max-width: 150px;
}

.movie__data {
    margin-left: 20px;
}

.movie_genres {
    display: flex;
}

.genres__genre {
    margin-right: 8px;
}

.movie__summary {
    font-weight: 300;
}