.search-movie {
    background-color: #EFEEEC;
    border-radius: 5px;
    padding: 16px;
    -webkit-box-shadow: 6px 6px 11px -4px rgba(204,204,204,1);
    -moz-box-shadow: 6px 6px 11px -4px rgba(204,204,204,1);
    box-shadow: 6px 6px 11px -4px rgba(204,204,204,1);
}

.search-movie a {
    display: grid;
    grid-template-columns: 110px 1fr;
    grid-gap: 20px;
    /* display: flex;
    justify-content: space-between;
    align-items: center; */
    text-align: left;
    text-decoration: none;
    color: #000;
}

.search-movie__image {
    position: relative;
    top: -50px;
    max-width: 110px;
}

.search-movie__subtitle {
    font-size: 14px;
}